label {
  display: block;
  margin-top: 10px;

}

.card-container.card {
  max-width: 550px !important;
  padding: 40px 40px;
}

.card {
  background-color: #f7f7f7;
  padding: 20px 25px 30px;
  margin: 0 auto 25px;
  margin-top: 50px;
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}

.profile-img-card {
  width: 96px;
  height: 96px;
  margin: 0 auto 10px;
  display: block;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}

.Todo-container{
  background:linear-gradient(to right bottom, #CDDED9,#CDDED9);
  width: 100%;
  min-height: 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-wrapper{
  background-color: #E8E9C1;
  min-width: 375px;
  min-height:650px;
  padding: 30px;
  box-sizing: 10px;
  box-shadow: 3px 6px 40px #000;
  margin-bottom: 10px;
}

.header h1{
  text-align: center;
  margin:30px 0;
}

.list-item{
  display: flex;
  margin:20px 0;
  border: 2px solid #4F6400;
  border-radius: 10px;
  padding: 10px;
  max-height: 80px;
}

.list-title{
  min-width: 80%;
  background-color: #E8E9C1;
  border: none;
  font-size: 28px;
  padding-left: 10px;
  margin-right: 15px;
}

.fun-button{
  position: relative;
}

.button-complete,
.button-delete,
.button-edit{
  border:none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 28px;
  border-radius: 50%;

}
.button-complete{
  color: #1C3902;
  margin-right:10px
}

.button-edit{
  color: #0288D1;
  margin-right:10px
}

.button-delete{
  color: #ff6c6c;
  margin-right:10px
}

.complete{
  text-decoration-style: solid;
  text-decoration-line: line-through;
  text-decoration-color: red;
  opacity: 0.6;
}

.button-add{
  width:100%;
  padding: 15px 15px;
  font-size: 20px;
  border-radius: 10px;
  border:1;
  background-color: #CCDDD7;
  cursor: pointer;

}

.task-input{
  outline: none;
  width: 100%;
  padding: 15px;
  margin-right: 25px;
  font-size: 20px;
  color: #000;
  background-color: #E8E9C1;
  border:1px solid #4F6400;
  border-radius: 10px;
}



input:focus,
button:focus{
  outline:none;
}